import React, { useEffect, useRef, Suspense } from "react";
import { Canvas } from "react-three-fiber";
import { OrbitControls } from "@react-three/drei";
import { useGLTF } from "@react-three/drei/useGLTF";
import { useTexture } from "@react-three/drei";
function Vase(props) {
  const group = useRef();
  const { nodes } = useGLTF("models/BakedModel/vase.glb");
  const matT = useTexture("images/BakedModel/firstTexture.png");
  const planeT = useTexture("images/BakedModel/planeTexture.png");
  const cubeT = useTexture("images/BakedModel/cubeTexture.png");
  //   cubeT.wrapS = THREE.RepeatWrapping;
  //   cubeT.wrapT = THREE.RepeatWrapping;
  //   cubeT.offset.set(-0.05, 0.665, 0);
  cubeT.offset.set(0, 0.67, 0);
  return (
    <group ref={group} {...props} dispose={null}>
      <mesh
        geometry={nodes.Plane.geometry}
        position={[-1.6, 0, 0]}
        rotation={[0, 0, -Math.PI / 2]}
        scale={[3.5, 3.5, 3.5]}
      >
        <meshStandardMaterial map={planeT} attach="material" />
      </mesh>
      <mesh
        castShadow
        receiveShadow
        geometry={nodes.vase.geometry}
        position={[0, -0.31, 0]}
        rotation={[0, 0, -Math.PI / 2]}
        scale={[0.08, 0.08, 0.08]}
      >
        <meshStandardMaterial map={matT} attach="material" />
      </mesh>

      <mesh
        geometry={nodes.Cube.geometry}
        rotation={[0, -Math.PI, 0]}
        position={[0, -0.88, 0]}
        scale={[0.4, 0.4, 0.43]}
      >
        <meshStandardMaterial attach="material" map={cubeT} />
      </mesh>
    </group>
  );
}
function BakedModel() {
  useEffect(() => {
    document.title = "Baked Model";
    return () => {
      document.title = "WebGlfun";
    };
  }, []);
  let s = 3;
  return (
    <Canvas
      gl={{ antialias: false, alpha: false }}
      camera={{ position: [0, 0, 5.5], near: 0.001, far: 1000, fov: 50 }}
      onCreated={({ gl }) => {
        gl.setClearColor("black");
        gl.setPixelRatio(window.devicePixelRatio);
      }}
    >
      <ambientLight intensity={1} />
      <pointLight position={[0, 80, 1]} intensity={3} />
      <Suspense fallback={null}>
        <Vase rotation={[0, -Math.PI / 2, 0]} scale={[s, s, s]} />
      </Suspense>
      <OrbitControls />
    </Canvas>
  );
}

export default BakedModel;
